
import './recapcards.css';


function RecapCards() {
    
    return (
        <div>
            <div className='recapcards rc1 grid place-items-center'>
                <h2 className='recapcardstitle'>
                    Qui sommes nous ?
                </h2>
                <p>
                    Retrouvez nos missions, notre territoire et notre quipe.
                </p>
                <div className='recapcardsbtn rcb1 grid place-items-center'>
                    <p className='recapcardsbtntext'>
                        L'quipe
                    </p>
                </div>
            </div>
            <div className='recapcards rc2 grid place-items-center'>
                <h2 className='recapcardstitle'>
                    Vous tes patient ?
                </h2>
                <p>
                    Retrouvez ce que la CPTS fait pour vous !
                </p>
                <div className='recapcardsbtn rcb2 grid place-items-center'>
                    <p className='recapcardsbtntext'>
                        Nos protocoles
                    </p>
                </div>
            </div>
            <div className='recapcards rc3 grid place-items-center'>
                <h2 className='recapcardstitle'>
                    A la une
                </h2>
                <p>
                    Retrouvez les annonces et informations, pour vous, professionnels de sant
                </p>
                <div className='recapcardsbtn rcb3 grid place-items-center'>
                    <p className='recapcardsbtntext'>
                        Nos actualits
                    </p>
                </div>
            </div>
            <div className='recapcards rc4 grid place-items-center'>
                <h2 className='recapcardstitle'>
                    Vous tes professionnel ?
                </h2>
                <p>
                    Retrouvez lactualit de la CPTS et des partenaires sant du territoire.
                </p>
                <div className='recapcardsbtn rcb4 grid place-items-center'>
                    <p className='recapcardsbtntext'>
                        Nos protocoles
                    </p>
                </div>
            </div>
        </div>
    );
}

export default RecapCards;